@import '../../styles/variables.scss';
@import '../../styles/mixins.scss';

.MuiBox-root.login-wrap {
    @media (min-width: 1024px) {
        width: 25%;
    }

    h3 {
        padding: 10px;
        text-align: center;
        font-size: 28px;
        font-weight: 500;
        color: #fff;
        margin-bottom: 15px;
    }

    &>p {
        color: #fff;
        font-weight: 400;
        text-align: center;

        &>a {
            color: $icon-chat-color-weird;
            font-weight: 700;
        }
    }
}

.error-auth {
    text-align: center;
}